.navbar {
    padding: 20px;
    display: flex;
    justify-content: space-around;
    border-bottom: 2px solid #fff;
}
.navbar.active{
    background: #000;
    padding: 20px;
}
.navbar-brand {
    font-size: 3em;
    color: #fff !important;
    padding: 0px 60px;
}
.navbar-brand span{
    font-size: 1.6em;
    color: var(--main) !important;
}
.navbar-nav {
    padding: 0px 60px;
    margin-top: 50px;
}
.nav-link {
    color : #fff !important;
}
.nav-link:hover {
    color: var(--main) !important;
}